
import Image from 'next/image';
import { PageSectionTitle } from '@/components/page-section-title';
import { SectionWrapper } from '@/components/section-wrapper';
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { Github } from 'lucide-react';

interface GitHubRepo {
  id: number;
  name: string;
  description: string | null;
  html_url: string;
  stargazers_count: number;
  forks_count: number;
  language: string | null;
  pushed_at: string; // Used for sorting
}

async function getGithubProjects(username: string): Promise<GitHubRepo[]> {
  if (!username || username === 'seu-usuario-padrao') { // Use um placeholder diferente para evitar conflito se o usuário realmente se chamar 'seu-usuario'
    console.warn("GitHub username is not set or is the default placeholder. Skipping API call.");
    return [];
  }
  try {
    // Fetch top 6 repositories sorted by last push date
    const response = await fetch(`https://api.github.com/users/${username}/repos?sort=pushed&per_page=6&type=owner`);
    if (!response.ok) {
      console.error(`Error fetching GitHub projects: ${response.status} ${response.statusText}`);
      return [];
    }
    const data = await response.json();
    return data as GitHubRepo[];
  } catch (error) {
    console.error("Failed to fetch GitHub projects:", error);
    return [];
  }
}

export async function PortfolioSection() {
  const githubUsername = 'viniciusperico'; 
  const projects = await getGithubProjects(githubUsername);

  return (
    <SectionWrapper id="projetos">
      <PageSectionTitle
        title="Meus Projetos no GitHub"
      />
      {projects.length === 0 && githubUsername === 'seu-usuario-padrao' && (
        <div className="text-center text-foreground/70 bg-card/50 p-6 rounded-lg shadow">
          <p className="mb-2">Por favor, configure seu nome de usuário do GitHub em <code>src/components/sections/portfolio-section.tsx</code> para ver seus projetos.</p>
          <p>Abra o arquivo e substitua <code>'seu-usuario-padrao'</code> pelo seu nome de usuário.</p>
        </div>
      )}
      {projects.length === 0 && githubUsername !== 'seu-usuario-padrao' && (
         <div className="text-center text-foreground/70 bg-card/50 p-6 rounded-lg shadow">
          <p>Nenhum projeto público encontrado ou falha ao buscar projetos para &quot;{githubUsername}&quot;.</p>
          <p>Verifique se o nome de usuário está correto e se possui repositórios públicos.</p>
        </div>
      )}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
        {projects.map((item) => (
          <Card key={item.id} className="overflow-hidden shadow-lg transition-all duration-300 hover:shadow-2xl hover:scale-105 flex flex-col bg-card/80 backdrop-blur-sm">
            <CardHeader className="p-0">
              <div className="relative aspect-video">
                <Image
                  src="https://placehold.co/600x400.png" // Placeholder, as GitHub API doesn't provide project images easily
                  alt={`Imagem do projeto ${item.name}`}
                  layout="fill"
                  objectFit="cover"
                  data-ai-hint="software project abstract"
                />
              </div>
            </CardHeader>
            <CardContent className="pt-6 flex-grow">
              <CardTitle className="text-xl font-semibold text-primary mb-2 font-headline">{item.name}</CardTitle>
              <p className="text-foreground/80 text-sm leading-relaxed h-20 overflow-y-auto">
                {item.description || 'Sem descrição disponível.'}
              </p>
            </CardContent>
            <CardFooter>
              <Button asChild variant="outline" className="w-full text-primary border-primary hover:bg-primary/10">
                <Link href={item.html_url} target="_blank" rel="noopener noreferrer">
                  Ver no GitHub <Github className="ml-2 h-4 w-4" />
                </Link>
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </SectionWrapper>
  );
}
